import React, { useState } from 'react';
import '../App.css';

const Tilaus = ({ newTilaus, setNewTilaus, calcHinta, tuotteet, finish }) => {
    const [newFirstName, setNewFirstName] = useState("");
    const [newLastName, setNewLastName] = useState("");
    const [newEmail, setNewEmail] = useState("");
    const [newNumber, setNewNumber] = useState("");
    const [newAddress, setNewAddress] = useState("");
    const [newPostNro, setNewPostNro] = useState("");
    const [newPostArea, setNewPostArea] = useState("");
    const handleSubmit = (e) => {
        e.preventDefault();
        setNewTilaus({firstname: newFirstName, lastname: newLastName, email: newEmail, puh: newNumber, address: newAddress, postnro: newPostNro, postarea: newPostArea});
    }
    return (
        <div>
            <form className="tilaus-form" id="tilaus-form" onSubmit={handleSubmit}>
                <p>Nimi:</p>
                <input className="tilaus-input" type="text" value={newFirstName} onChange={e => setNewFirstName(e.target.value)} placeholder="Etunimi"></input>
                <input className="tilaus-input" type="text" value={newLastName} onChange={e => setNewLastName(e.target.value)} placeholder="Sukunimi"></input><br/>
                <p>Sähköpostiosoite:</p>
                <input className="tilaus-input" type="text" value={newEmail} onChange={e => setNewEmail(e.target.value)} placeholder="Sähköpostiosoite"></input><br/>
                <p>Puhelinnumero:</p>
                <input className="tilaus-input" type="number" value={newNumber} onChange={e => setNewNumber(e.target.value)} placeholder="Puhelinnumero"></input><br/>
                <p>Osoite:</p>
                <input className="tilaus-input" type="text" value={newAddress} onChange={e => setNewAddress(e.target.value)} placeholder="Osoite"></input>
                <input className="tilaus-input" type="number" value={newPostNro} onChange={e => setNewPostNro(e.target.value)} placeholder="Postinumero"></input>
                <input className="tilaus-input" type="text" value={newPostArea} onChange={e => setNewPostArea(e.target.value)} placeholder="Postitoimipaikka"></input><br/>
                <br/><br/><br/>
                <input className="tilaus-button button" type="submit" value="Vahvista yhteystiedot" onClick={() => confirmOrder()}></input>
            </form>
            <TilausValmis tuotteet={tuotteet} newTilaus={newTilaus} calcHinta={calcHinta} finish={finish}/>
        </div>
    );
}

const TilausValmis = ({ newTilaus, calcHinta, tuotteet, finish }) => {
    const ostokset = tuotteet.filter(tuote => tuote.amount > 0);
    return(
        <div className="tilaus-finish" id="tilaus-finish">
            <h1>Tilauksesi on valmis!</h1>
            <h3>Tilauksen tiedot:</h3>
            <p>{newTilaus.firstname} {newTilaus.lastname}<br />
            {newTilaus.email}<br />
            {newTilaus.puh}<br />
            {newTilaus.address}<br />
            {newTilaus.postnro} {newTilaus.postarea}</p>
            <br />
            <div className="tilausTuotteet">
                {ostokset.map(tuote => <TilausTuote key={tuote.id} tuote={tuote}/>)}
            </div>
            <h2>Yhteishinta: {calcHinta()}.00€</h2>
            <br />
            <button className="button tilaus-end" onClick={() => finish()}>Sulje tilaus</button>
        </div>
    );
}

const TilausTuote = ({tuote}) => {
    return (
      <div className="ostos">
        <p className="tilaus-p"><b>{tuote.tuote}</b> Hinta: {tuote.hinta}.00€
        <b> x {tuote.amount}</b></p>
        <img className="tilaus-img" src={tuote.kuva} alt="tilaus-kuva"></img>
      </div>
    );
}

const confirmOrder = () => {
    document.getElementById("tilaus-form").style.display = "none";
    document.getElementById("tilaus-finish").style.display = "block";
}

export default Tilaus;