import React, { useState } from 'react';
import logo from './logo.png';
import Tuotteet from './components/Tuote.js';
import tuotteetlista from './components/Tuote_data.js';
import Ostoskori from './components/Ostoskori.js';
import Tilaus from './components/Tilaus.js';
import './App.css';

const App = () => {
  const [tuotteet, setTuotteet] = useState(tuotteetlista);
  let tilaus = {firstname: "", lastname:"", email:"", puh:"", address:"", postnro:"", postarea:""}
  const [newTilaus, setNewTilaus] = useState(tilaus);
  
  const addOne = (id) => {
    const tempTuotteet = tuotteet.map(tuote => {
        if(tuote.id === id){
            const tempTuote = {...tuote, amount: tuote.amount + 1};
            return tempTuote;
        } else{
            return tuote;
        }
    })
    setTuotteet(tempTuotteet);
  }
  const removeOne = (id) => {
    const tempTuotteet = tuotteet.map(tuote => {
        if(tuote.id === id){
            const tempTuote = {...tuote, amount: tuote.amount - 1};
            return tempTuote;
        } else{
            return tuote;
        }
    })
    setTuotteet(tempTuotteet);
  }
  const calcHinta = () => {
    return tuotteet.map(tuote => tuote.amount * tuote.hinta).reduce((a, b) => a + b, 0)
  }
  const display = () => {
    document.getElementById("tilaus").style.display = "block";
  }
  const stopdisplay = () => {
    document.getElementById("tilaus").style.display = "none";
  }
  const finish = () => {
    const tempTuotteet = tuotteet.map(tuote => {
      tuote = {...tuote, amount: 0};
      return tuote;
    });
    setTuotteet(tempTuotteet)
    document.getElementById("tilaus").style.display = "none";
  }

  return (
    <div className="App">
      <header className="App-header">
        <img src={logo} className="App-logo" alt="logo" />
        <p>Fanikauppa</p>
      </header>

      <section className="tuote-section">
        <Tuotteet tuotteet={tuotteet} setTuotteet={setTuotteet} addOne={addOne} />
      </section>

      <section className="ostoskori-section">
        <h2 className="pink">Ostoskori</h2>
        <Ostoskori tuotteet={tuotteet} setTuotteet={setTuotteet} addOne={addOne} removeOne={removeOne} display={display} calcHinta={calcHinta} />
      </section>

      <div className="tilaus" id="tilaus">
        <h2>Tilaus</h2> <button className="button x" onClick={() => stopdisplay()}>X</button>
        <Tilaus tilaus={tilaus} tuotteet={tuotteet} newTilaus={newTilaus} setNewTilaus={setNewTilaus} calcHinta={calcHinta} finish={finish} />
      </div>

      <footer className="footer">
        <small>Tiia Hakkarainen 1808TiviPO02</small>
      </footer>
    </div>
  );
}

export default App;
