import React from 'react';
import '../App.css';

const Tuote = ({tuote, addOne}) => {
    return (
      <div className="tuote">
        <img className="tuote-img" src={tuote.kuva} alt="tuote-kuva"></img>
        <h3>{tuote.tuote} </h3>
        {tuote.kuvaus}
        <h1 className="pink">{tuote.hinta}.00€</h1>
        <AddButton tuote={tuote} addOne={addOne} />
      </div>
    );
}

const Tuotteet = ({tuotteet, addOne}) => {
    return(
        <div className="tuotteet">
            {tuotteet.map(tuote => <Tuote key={tuote.id} tuote={tuote} addOne={addOne} />)}
        </div>
    );
}

const AddButton = ({ tuote, addOne }) => {
    return(
        <button className="button" onClick={() => addOne(tuote.id)}>Lisää ostoskoriin + </button> 
    );
}
  
export default Tuotteet;