import React from 'react';
import '../App.css';

const Ostoskori = ({ tuotteet, addOne, removeOne, display, calcHinta }) => {
    return (
        <div>
            <div className="ostoskori-ostokset">
                <OstoskoriTuotteet tuotteet={tuotteet} addOne={addOne} removeOne={removeOne}/>
            </div>
            <div className="ostoskori-button">
                <button className="ostoskori-button" onClick={() => display()}>Vahvista tilaus</button>
            </div>
            <div className="ostoskori-hinta">
                Yhteensä: {calcHinta()}.00€
            </div>
        </div>
    );
}

const OstoskoriTuote = ({tuote, addOne, removeOne}) => {
    return (
      <div className="ostos">
        <p className="ostos-p"><b>{tuote.tuote}</b> Hinta: {tuote.hinta}.00€
        <b> x {tuote.amount}</b> <AddButton tuote={tuote} addOne={addOne} /> <RemoveButton tuote={tuote} removeOne={removeOne} /></p>
        <img className="ostos-img" src={tuote.kuva} alt="ostos-kuva"></img>
      </div>
    );
}
const OstoskoriTuotteet = ({ tuotteet, addOne, removeOne }) => {
    const ostokset = tuotteet.filter(tuote => tuote.amount > 0);
    if(ostokset.length){
        return (
            <div className="OstoskoriTuotteet">
                {ostokset.map(tuote => <OstoskoriTuote key={tuote.id} tuote={tuote} addOne={addOne} removeOne={removeOne} />)}
            </div>
        )
    }
    else{
        return (
            <div>
                <p className="ostoskori-empty">Ostoskorisi on tyhjä</p>
                <p className="ostoskori-empty">Lisää tuotteita ostoskoriin painamalla [Lisää ostoskoriin +] nappia tuotteen alapuolella!</p>
            </div>
        )
    }
}

const AddButton = ({ tuote, addOne }) => {
    return(
        <button className="button" onClick={() => addOne(tuote.id)}> + </button> 
    );
}
const RemoveButton = ({ tuote, removeOne }) => {
    return(
        <button className="button" onClick={() => removeOne(tuote.id)}> - </button> 
    );
}

export default Ostoskori;